.weather {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .day {
    margin: 1rem 0;
    font-weight: normal;
    font-size: 1rem;
  }
  
  .icon {
    width: 50px;
  }
  
  .temperature {
    font-size: 1.25rem;
    margin-top: 1rem;
    color: rgba(0, 0, 0, 0.5);
  }
  
  @media screen and (min-width: 1024px) {
    .icon {
      width: 70px;
    }
  }
  