.forecast {
    padding: 24px 36px;
    order: -1;
  }
  
  .header {
    margin: 0;
    margin-bottom: 0.5rem;
    letter-spacing: 2px;
    font-weight: 300;
    font-size: 1rem;
  }
  
  .weathers {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 280px;
  }
  
  @media screen and (min-width: 1024px) {
    .forecast {
      padding: 0 48px;
      flex: 1;
      order: 1;
    }
  
    .header {
      margin-bottom: 1rem;
      font-size: 1.5rem;
    }
  }